<template>
    <div class="work-review-container">
        <el-card style="min-height:600px;">
            <el-tabs v-model="workReview">
                <el-tab-pane label="" name="workReview">
                    <el-row>
                        <el-col>
                            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                                <el-form-item label="">
                                    <el-input v-model="formInline.activityType" placeholder=""></el-input>
                                </el-form-item>
                                
                                <el-form-item label="">
                                    <el-input v-model="formInline.activityName" placeholder=""></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="goToSearch"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="3">
                            <el-button type="primary" plain></el-button>
                        </el-col>
                        <el-col :span="3">
                            <el-button type="primary" plain @click="goToImport"></el-button>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-table
                            ref="multipleTable"
                            :data="tableData"
                            border
                            :header-cell-style="{background:'#EEEEEE',color:'#323232'}"
                            tooltip-effect="dark"
                            style="width: 100%"
                            @selection-change="tableSelectionChange">
                            <el-table-column
                            type="selection"
                            width="55">
                            </el-table-column>
                            <el-table-column
                            label=""
                            type="index"
                            width="160"
                            >
                            </el-table-column>
                            <el-table-column
                            prop="name"
                            label=""
                            >
                            </el-table-column>
                            
                            <el-table-column
                            label=""
                            prop="score">
                            </el-table-column>

                            <el-table-column
                            prop="comment"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column
                            fixed="right"
                            label=""
                            width="100">
                            <template slot-scope="scope">
                                <el-button @click="goToLook(scope.row)" type="text" size="small"></el-button>
                            </template>
                            </el-table-column>
                        </el-table>
                    </el-row>
                    <el-row class="page-div">
                        <el-pagination
                        @size-change="pageSizeChange"
                        @current-change="pageCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="10"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="40">
                        </el-pagination>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
            workReview:'workReview',
            formInline: {
                activityName: '',
                activityType:''
            },
            tableData: [{
                name: '',
                score: '70',
                comment:'  '
                }, {
                score: '80',
                name: '',
                comment:'  '
                }, {
                score: '75',
                name: '',
                comment:'  '
                }, {
                score: '90',
                name: '',
                comment:'  '
                }, {
                score: '85',
                name: '',
                comment:'  '
                }, {
                score: '78',
                name: '',
                comment:'  '
                }, {
                score: '59',
                name: '',
                comment:'  '
                }
            ],
            multipleSelection: [],
            currentPage:1,//
        }
    },
    mounted(){

    },
    methods:{
        //
        goToSearch(){

        },
        //
        goToImport(){
            this.$router.push({
                path:'/importWorkList'
            })
        },
        //
        goToLook(){
            this.$router.push({
                path:'/workScore'
            })
        },
        //
        pageSizeChange(val){

        },
        //
        pageCurrentChange(val){

        },
        //
        open(index,rows) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                rows.splice(index, 1);
                this.$message({
                    type: 'success',
                    message: '!'
                });
                
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        }
    }
}
</script>
<style lang="less" scoped>
.work-review-container{
    width: 1180px;
    margin: auto;
    margin-top: 20px;
    .page-div{
        display:flex;
        justify-content:center;
    }
}
</style>
