<template>
  <div class="my-editer">
    <!-- <el-card style="height: 610px;"> -->
      <quill-editor v-model="content" ref="myQuillEditor" style="height: 250px;" @change="editorChange" :options="editorOption">
        <!-- toolar -->
        <div id="toolbar" slot="toolbar">
          <!-- Add a bold button -->
          <button class="ql-bold" title="">Bold</button>
          <button class="ql-italic" title="">Italic</button>
          <button class="ql-underline" title="">underline</button>
          <button class="ql-strike" title="">strike</button>
          <button class="ql-blockquote" title=""></button>
          <button class="ql-code-block" title=""></button>
          <button class="ql-header" value="1" title="1"></button>
          <button class="ql-header" value="2" title="2"></button>
          <!--Add list -->
          <button class="ql-list" value="ordered" title=""></button>
          <button class="ql-list" value="bullet" title=""></button>
          <!-- Add font size dropdown -->
          <select class="ql-header" title="">
            <option selected></option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
          </select>
          <select class="ql-size" title="">
            <option value="10px">10px</option>
            <option value="12px">12px</option>
            <option value="14px">14px</option>
            <option value="16px" selected>16px</option>
            <option value="18px">18px</option>
            <option value="20px">20px</option>
          </select>
          <select class="ql-font" title="">
            <option value="SimSun"></option>
            <option value="SimHei"></option>
            <option value="Microsoft-YaHei"></option>
            <option value="KaiTi"></option>
            <option value="FangSong"></option>
            <option value="Arial">Arial</option>
          </select>
          <!-- Add subscript and superscript buttons -->
          <select class="ql-color" value="color" title=""></select>
          <select class="ql-background" value="background" title=""></select>
          <select class="ql-align" value="align" title=""></select>
          <button class="ql-clean" title=""></button>
          <!-- You can also add your own -->
        </div>
      </quill-editor>
    <!-- </el-card> -->
  </div>
</template>

<script>
  import {
    Quill,
    quillEditor
  } from 'vue-quill-editor'
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
  
  //font.css 
  import '../assets/css/font.css'
  
  // 
  let Size = Quill.import('attributors/style/size')
  Size.whitelist = ['10px', '12px', '14px', '16px', '18px', '20px']
  Quill.register(Size, true)

  // 
  var fonts = ['SimSun', 'SimHei', 'Microsoft-YaHei', 'KaiTi', 'FangSong', 'Arial', 'Times-New-Roman', 'sans-serif',
    '', ''
  ]
  var Font = Quill.import('formats/font')
  Font.whitelist = fonts
  Quill.register(Font, true)

  export default {
    name: 'FuncFormsEdit',
    components: {
      quillEditor
    },
    data() {
      return {
        content: null,
        editorOption: {
          placeholder: "",
          theme: "snow", // or 'bubble' 
          modules: {
            toolbar: {
              container: '#toolbar'
            }
          }
        }
      }
    },
    methods:{
      //
      editorChange(){
        // console.log("content===",this.content)
        this.$emit('editorChange',this.content)
      }
    }
  }
</script>

<style lang='less'>

.ql-container{
  height:80%;
  
}
</style>

