<template>
    <div class="work-score-container">
        <el-card style="min-height:600px;">
            
            <el-tabs v-model="activeName">
                <el-tab-pane label="" name="look">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form" label-width="100px">
                                <el-form-item label="">
                                    <el-col>{{form.activityName}}</el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col>{{form.introduction}}</el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col>{{form.regForm}}</el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col>{{form.annex}}</el-col>
                                </el-form-item>    
                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>

            <el-tabs v-model="activeDetail">
                <el-tab-pane label="" name="detail">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form2" label-width="100px">
                                <el-form-item label="">
                                    <el-input
                                    placeholder=""
                                    v-model="form2.scope"
                                    clearable
                                    style="width:300px;">
                                    </el-input>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-input
                                    type="textarea"
                                    :rows="6"
                                    placeholder=""
                                    v-model="form2.introduction">
                                    </el-input>
                                </el-form-item>

                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <el-row>
                <el-col :span="2" :offset="11">
                    <el-button type="primary" @click="goToScope"></el-button>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
            activeName:'look',
            activeDetail:'detail',
            form: {
                activityName: '',
                introduction:'',
                regForm:'.doc',
                annex:'.mp4'
            },
            form2:{
                scope:'',//
                introduction:'',//
            },
            
        }
    },
    mounted(){

    },
    methods:{
        //
        goToScope(){
            this.open()
        },
        //
        open() {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                center: true
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        }
    }
}
</script>
<style lang="less" scoped>
.work-score-container{
    width:1180px;
    margin:auto;
    margin-top:20px;
}
</style>
