<template>
      <div style=" clear: both;text-align: left;">
          <el-form ref="form" :model="form" :inline="true">
            <el-form-item label="">
              <el-select v-model="form.addr" placeholder="" >
                <el-option v-for="item in AddrList" :key="item.id" :label="item.name" :value="item.id">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.schoolname" placeholder="" >
                <el-option v-for="item in SchoolList" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.workname" placeholder=""> </el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.phone" placeholder=""> </el-input>
            </el-form-item>
            <el-form-item label="">
               <el-input v-model="form.author" placeholder=""> </el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit()"></el-button>
            </el-form-item> 
          </el-form>
          <!---->
          <div class="buttonenum">
             <el-button size="medium" type="primary" @click="fenpeiwork()"></el-button>
             <el-button size="medium"></el-button>
             <el-button size="medium"></el-button>
          </div>
          <div style="margin-top:16px;">
            <template>
              <!--       -->
              <el-table :data="tableData" stripe style="width: 100%">
                <el-table-column
                  label=""
                  type="index"
                   width="180"
                  align="center">
                </el-table-column>
                <!-- name author address shcoolname private -->
                <el-table-column
                  prop="name"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="author"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="shcoolname"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="private"
                  label=""
                  width="180">
                </el-table-column>
                
                <!-- <el-table-column
                  prop="address"
                  label=""
                  width="180">>
                </el-table-column>
                <el-table-column
                  prop="shcoolname"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="private"
                  label=""
                  width="180">
                </el-table-column> -->
              </el-table>
            </template>
          </div>
           <div class="block">
              
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage1"
                :page-sizes="[10,20]"
                :page-size="10"
                layout="total, sizes, prev, pager, next, jumper"
                :total="10">
              </el-pagination>
            </div>

                <el-dialog
                  :visible.sync="dialogVisible"
                  width="40%"
                  :before-close="handleClose">
                  
                  <el-transfer
                    filterable
                    :filter-method="filterMethod"
                    filter-placeholder=""
                    v-model="value"
                    :data="data">
                  </el-transfer>
                    
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogVisible = false"> </el-button>
                  </span>
                </el-dialog>
     
      </div>
      
    
</template>
<script>
  export default {
    data() {
        const generateData = _ => {
        const data = [];
       
        const cities = ['', '', '', '', '', '', '','',""];
        const pinyin = ['', '', '', '', '', '', '','',""];
       
        cities.forEach((city, index) => {
          data.push({
            label: city,
            key: index,
            pinyin: pinyin[index]
          });
        });
        return data;
      };
      return {
            data: generateData(),
            value: [],
            filterMethod(query, item) {
              return item.pinyin.indexOf(query) > -1;
            },
            form:{
              addr:'',
              schoolname:'',
              workname:'',
              phone:'',
              author:'',
            
            },
            AddrList:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
            ],
            SchoolList:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
            ],
            //     
            tableData:[
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
                {name:'',author:'', address:'', shcoolname:"",private:""},
            ],
              currentPage1: 1,
              
              dialogVisible:false,
            
      };
    },
    methods: {
        handleClose(done) {
          this.$confirm('')
            .then(_ => {
              done();
            })
            .catch(_ => {});
        },
      
      fenpeiwork(){
        if(this.dialogVisible==true){
           this.dialogVisible=false;
        }else{
          this.dialogVisible=true;
        }
       
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      }

    }
  };
</script>
<style lang="less" scoped>
  .secendROw{
        margin-left: -126px;
  }
  .block{
    margin-top:16px;
    margin-left:276px;
  }
